import React from 'react'
import posed from 'react-pose'
import { tween } from 'popmotion'
import { interpolate } from 'flubber'
import styled from 'styled-components'

const StyledBody = styled.div`
  height: 40vh;
  padding: 100px 0;
  flex-flow: column;
  justify-content: stretch;
  align-items: center;
  display: flex;
  background: #f4f3fa;

  /* #root {
    color: #fff;
    font-size: 32px;
    font-family: PT Sans, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 200;
    text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
  } */

  button {
    display: flex;
    border: none;
    margin-bottom: 20px;
    width: auto;
    overflow: visible;
    background: transparent;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    -webkit-appearance: none;
    background: transparent;
    outline: none;
    color: white;
    padding: 15px 20px;
    cursor: pointer;
    color: rgb(70, 200, 120);
    font-size: 2rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
  }
`

const paths = {
  plane:
    'M187.57,0C291.16,0,375.13,67.75,375.13,151.32c0,33.35-13.41,64.17-36.07,89.17l34.85,86.65L226.21,297.46c-15.43.42-32.72,4.16-50,4.61-2,.05-3.92.08-5.86.08C69.88,302.15,0,233.3,0,151.32,0,67.75,84,0,187.57,0',
  heart:
    'M352.35,0A39.15,39.15,0,0,1,391.5,39.15v234.9a39.15,39.15,0,0,1-39.15,39.15H39.15A39.15,39.15,0,0,1,0,274.05V39.15A39.27,39.27,0,0,1,39.15,0ZM266.81,178.13,391.5,274.05V234.9L291.28,158.56,391.5,78.3V39.15L195.75,195.75,0,39.15V78.3l100.22,80.26L0,234.9v39.15l124.69-95.92,71.06,56.77Z',
}
const text = {
  first: 'Refreshingly Simple Text Message Appointment Reminders',
  second: 'Calendar',
  third: 'Perfect Collection of business tools to help you grow',
}
const textIds = Object.keys(text)
const pathIds = Object.keys(paths)

const morphTransition = ({ from, to }) =>
  tween({
    from: 0,
    to: 1,
  }).pipe(interpolate(from, to))

const Icon = posed.path(
  pathIds.reduce((config, id) => {
    config[id] = {
      d: paths[id],
      transition: morphTransition,
    }

    return config
  }, {}),
)

const NextButton = posed.button({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.1 },
  press: { scale: 0.9 },
})

class IconMorph extends React.Component {
  state = {
    pathIndex: 0,
    buttonText: text.first,
  }

  gotoNext = () => {
    const { pathIndex } = this.state
    const nextIndex = pathIndex + 1
    this.setState({
      pathIndex: nextIndex > pathIds.length - 1 ? 0 : nextIndex,
    })
  }

  render() {
    return (
      <StyledBody>
        <svg
          width="120"
          height="120"
          viewBox="0 0 375.13 327.15"
          fill="rgb(70, 200, 120)"
        >
          <Icon pose={pathIds[this.state.pathIndex]} />
        </svg>
        <NextButton onClick={this.gotoNext}>{this.state.buttonText}</NextButton>
      </StyledBody>
    )
  }
}

export default IconMorph
